// import { fillRect } from 'gdxjs';

export const createRenderRewardSystem = () => {
  let rewardArray = [];
  return {
    init(worldState, { assets, width, height }) {
      const atlas = assets.psAtlas;
      const reward1 = atlas.findRegion('hop_qua1', -1);
      const reward2 = atlas.findRegion('hop_qua2', -1);
      const reward3 = atlas.findRegion('hop_qua3', -1);
      rewardArray = [
        {
          texTure: reward1,
          width: width / 6,
          height: ((width / 6) * reward1.height) / reward1.width
        },
        {
          texTure: reward2,
          width: width / 6,
          height: ((width / 6) * reward2.height) / reward2.width
        },
        {
          texTure: reward2,
          width: width / 6,
          height: ((width / 6) * reward3.height) / reward3.width
        }
      ];
    },
    update(
      delta,
      { rewards, playerState: { x }, gap },
      { batch, width, height, camera, whiteTex }
    ) {
      camera.setPosition(x - gap, 0);
      batch.setProjection(camera.combined);
      batch.begin();
      batch.setColor(1, 1, 1, 1);
      for (let reward of rewards) {
        if (!reward.hit) {
          const rw = rewardArray[reward.type];
          // fillRect(
          //   batch,
          //   whiteTex,
          //   reward.x,
          //   reward.y,
          //   reward.width,
          //   reward.height
          // );
          rw.texTure.draw(
            batch,
            reward.x + reward.width / 2 - rw.width / 2,
            reward.y + reward.height / 2 - rw.height / 2,
            rw.width,
            rw.height
          );
        }
      }
      batch.end();
    }
  };
};
